import VueAxios from 'vue-axios'
import axios from 'axios'
import Vue from 'vue'
import store from '../store'
import router from '../router'

axios.defaults.baseURL = process.env.VUE_APP_API

// axios æ””æˆªå™¨ï¼Œç•¶ axios è«‹æ±‚å®Œæˆæ™‚ä½¿ç”¨çš„ function
// axios.xxx --> æ””æˆªå™¨ --> å‘¼å«åœ°æ–¹çš„ then æˆ– catch
axios.interceptors.response.use((response) => {
  return response
}, (error) => {
  // å¦‚æžœ axios è«‹æ±‚æœ‰å›žå‚³æ±è¥¿
  if (error.response) {
    if (error.response.status === 401) {
      const extendUrl = '/users/extend'
      // å¦‚æžœåŽŸå§‹è«‹æ±‚çš„ä½å€ä¸æ˜¯å»¶é•·ç™»å…¥çš„è©±ï¼Œæ‰å»¶é•·ç™»å…¥
      if (error.config.url !== extendUrl) {
        const originalRequest = error.config
        // å‚³é€å»¶é•·ç™»å…¥è«‹æ±‚
        return axios.post(extendUrl, {}, {
          headers: {
            authorization: 'Bearer ' + store.state.jwt.token
          }
        }).then((response) => {
          store.commit('extend', response.data.result)
          originalRequest.headers.authorization = 'Bearer ' + store.state.jwt.token
          return axios(originalRequest)
        }).catch((error) => {
          store.commit('logout')
          router.push('/login')
          return Promise.reject(error)
        })
      }
    }
  } else {
    if (error.code === 'ECONNABORTED' && error.message && error.message.includes('timeout')) {
      alert('è«‹æ±‚é€¾æ™‚')
    } else {
      alert('ç¶²è·¯ä¸ç©©å®š')
    }
  }
  return Promise.reject(error)
})

Vue.use(VueAxios, axios)
